.nav__Hero {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  background-color: var(--color5);
  width: 100%;
  height: 76px;
  position: relative;
  top: 95px;
  /* top: 10%; */
}

@media (max-width: 790px) {
  .nav__Hero {
    top: 80px;
  }
}

.nav__Hero ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  width: 55%;
}

@media (max-width: 790px) {
  .nav__Hero ul {
    list-style: none;
    display: none;
  }
}

.nav__Hero ul li {
  padding-inline: 8px;
}

.nav__Hero ul li a {
  font-weight: 700;
  font-size: 18px;
  line-height: 21.09px;
  text-decoration: none;
  color: #ffffcc;
}

.nav__Hero .icon__search {
  padding-inline: 8px;
  display: none;
}

.nav__Hero .contan__book {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 272px;
  height: 76px;
  flex-shrink: 0;
  background-color: var(--color4);
}

@media (max-width: 992px) {
  .nav__Hero .contan__book {
    width: 230px;
  }
}
@media (max-width: 575px) {
  .nav__Hero .contan__book {
    width: 170px;

  }
  .nav__Hero {
  width: 95%;
  margin: auto;
  }
}


.nav__Hero .contan__book .icon__dach {
    width: 25px;
    margin-inline: 8px;
}



.nav__Hero .contan__book .book__now {
  font-weight: 700;
  font-size: 18px;
  line-height: 21.09px;
  text-decoration: none;
  color: #ffffcc;
}

/* خاص بالإيكون منيو البداية من هنا */
.icon_menuo {
  width: 27px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  display: none;
}
@media (max-width: 790px) {
  .icon_menuo {
    list-style: none;
    display: flex;
  }
}

.icon_menuo span {
  background-color: #333;
  margin-bottom: 5px;
  height: 2px;
  width: 100%; 
}